import React from 'react';
import PT from 'prop-types';
import { Link, graphql } from 'gatsby';
import { DiscussionEmbed } from 'disqus-react';
import Layout from '../components/layout';
import SEO from '../components/seo';
import { rhythm, scale } from '../utils/typography';
import styles from './blog-post.module.scss';
import { CardBox } from '../components/cardbox';

const disqusShortname = 'martinschnurer-net-1';

const BlogPostTemplate = (props) => {
  const post = props.data.markdownRemark;
  const { title: siteTitle } = props.data.site.siteMetadata;
  const { previous, next } = props.pageContext;
  const { location } = props;
  return (
    <>
      <Layout
        location={location}
        title={siteTitle}
      >
        <CardBox>
          <div className={styles.blog_post}>
            <SEO
              title={post.frontmatter.title}
              description={post.frontmatter.description || post.excerpt}
            />
            <h1>{post.frontmatter.title}</h1>
            <p
              style={{
                ...scale(-1 / 5),
                display: 'block',
                marginBottom: rhythm(1),
                marginTop: rhythm(-0.5),
              }}
            >
              {post.frontmatter.date}
            </p>
            <div dangerouslySetInnerHTML={{ __html: post.html }} />
            <hr
              style={{
                marginBottom: rhythm(1),
              }}
            />
            <div className={styles.bottomNavigation}>
              <div className={styles.navigationLeft}>
                {previous && (
                  <Link to={previous.fields.slug} rel="prev">← {previous.frontmatter.title}</Link>
                )}
              </div>
              <div className={styles.navigationRight}>
                {next && (
                  <Link to={next.fields.slug} rel="next">{next.frontmatter.title} →</Link>
                )}
              </div>
            </div>
          </div>
        </CardBox>
        <CardBox style={{ marginTop: 15, marginBottom: 25 }}>
          <div className={styles.discuss_box}>
            <DiscussionEmbed
              shortname={disqusShortname}
              config={{
                identifier: post.id,
                title: post.frontmatter.title,
              }}
            />
          </div>
        </CardBox>
      </Layout>
    </>
  );
};


BlogPostTemplate.propTypes = {
  location: PT.objectOf(PT.any).isRequired,
  data: PT.objectOf(PT.any).isRequired,
  pageContext: PT.objectOf(PT.any).isRequired,
};

export default BlogPostTemplate;

export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    site {
      siteMetadata {
        title
        author
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      excerpt(pruneLength: 160)
      html
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        categories
      }
    }
  }
`;
